import React from "react";

const SearchResults = ({ results }) => {
  return (
    <div style={{ margin: "20px", textAlign: "left" }}>
      {results.length > 0 ? (
        results.map((item) => (
          <div
            key={item.id}
            style={{
              border: "1px solid #ccc",
              margin: "10px auto",
              padding: "10px",
              maxWidth: "800px", // Menambah lebar box
              width: "100%", // Membuat box mengikuti lebar kontainer
              borderRadius: "8px",
            }}
          >
            <h3 style={{ fontSize: "16px", fontFamily: "'Poppins', sans-serif" }}>
              <a
                href={item.link}
                style={{
                  textDecoration: "underline", // Menambahkan garis bawah
                  color: "black", // Mengubah warna link menjadi hitam
                  fontWeight: "500", // Sedikit tebal
                }}
              >
                {item.title}
              </a>
            </h3>
          </div>
        ))
      ) : (
        <div style={{ textAlign: "center" }}>
          <p style={{ fontSize: "14px", fontFamily: "'Poppins', sans-serif", color: "black", marginTop: "20px" }}>
            No results found.
          </p>
        </div>
      )}
    </div>
  );
};

export default SearchResults;
