import React, { useState } from "react";

const SearchBar = ({ onSearch }) => {
    const [query, setQuery] = useState("");
    const [category, setCategory] = useState("all");

    const handleSearch = () => {
        onSearch(query, category);
    };

    return (
        <div style={{ textAlign: "center", margin: "20px" }}>
            <select
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                style={{
                    padding: "10px",
                    fontSize: "16px",
                    border: "1px solid #ccc",
                    borderRadius: "8px",
                    marginRight: "10px",
                    outline: "none",
                }}
            >
                <option value="0">All</option>

            </select>
            <input
                type="text"
                placeholder="Search..."
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                style={{
                    padding: "10px 15px",
                    width: "300px",
                    fontSize: "16px",
                    border: "1px solid #ccc",
                    borderRadius: "8px",
                    outline: "none",
                    transition: "all 0.3s ease",
                    boxShadow: "0px 2px 4px rgba(0, 0, 0, 0.1)",
                }}
                onFocus={(e) => (e.target.style.borderColor = "#001F54")}
                onBlur={(e) => (e.target.style.borderColor = "#ccc")}
            />
            <button
                onClick={handleSearch}
                style={{
                    padding: "10px 20px",
                    marginLeft: "10px",
                    backgroundColor: "#001F54",
                    color: "white",
                    border: "none",
                    borderRadius: "8px",
                    fontSize: "16px",
                    cursor: "pointer",
                    transition: "all 0.3s ease",
                }}
                onMouseEnter={(e) => (e.target.style.backgroundColor = "#00296B")}
                onMouseLeave={(e) => (e.target.style.backgroundColor = "#001F54")}
            >
                Search
            </button>
        </div>
    );
};

export default SearchBar;
